#myabout {
    width: 100%;
    height: max-content;
    .box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .myimg{
            width: 300px;
            height: 380px;
            .avatar{
                width: 100%;
                height: 100%;
                background-size: cover;
                background-position: center;
            }
        }
    }

    h1 {
        text-align: center;
        font-size: 30px;
        font-weight: 900;
    }

    h3 {
        text-align: center;
        margin-bottom: 15%;
    }

    .content {
        display: flex;

        li {
            padding: 10px;
            list-style: square;
        }
    }
}