#mycontact{
    width: 100%;
    height: 90vh;
    h1{
        text-align: center;
        font-size: 30px;
        font-weight: 900;
        margin-bottom: 10%;
    }
    .card_contact{
        display: flex;
        width: 100%;
        .card{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 33%;
            background-color: rgba(94, 86, 86, 0.21);
            margin: 10px;
            height: max-content;
            padding: 10px;
            border-radius: 15px 0 15px;
            svg{
                margin-top: 5%;
            }
        }
        .card:nth-child(2){
            border-radius: 0 15px ;
        }
    }
}