#myresume {
    width: 100%;
    height: 100vh;

    h1 {
        text-align: center;
        font-size: 30px;
        font-weight: 900;
    }

    .timeline {
        width: 100%;
        border-left: 2px solid rgb(216, 206, 206);
        position: relative;
        .contaiter {
            position: relative;
            width: 100%;
            margin-left: 2%;
        }
    }

    .timeline::after {
        position: absolute;
            left: -5px;
            width: 10px;
            height: 10px;
            border-radius: 10px;
            background-color: #ffcc0d;
            color: white;
            content: '';
            top: 0;
    }
}