*,
html {
    color: white;

    font-size: 20px;

    li {
        list-style: none;
    }

    a {
        text-decoration: none;
        color: white;
        cursor: pointer;
    }

    .btn {
        background-color: rgb(15, 14, 14);
        display: flex;
        width: max-content;
        border-radius: 9px;

        a {
            padding: 20px;
        }
    }

    .btn:hover {
        opacity: 0.6;
    }

    html {
        scroll-behavior: smooth;
    }

    body {
        background-color: rgb(22, 22, 22);
        font-family: Jost, Arial, Helvetica, sans-serif;
        overflow: -moz-hidden-unscrollable;
        scroll-behavior: smooth;

        .main {
            display: flex;
            width: 100%;
            height: 90vh;

            .left-bar {
                display: flex;
                justify-content: center;
                position: relative;
                width: 25%;
                margin: 50px 25px;

                .fixed {
                    display: flex;
                    position: fixed;
                    margin: 0 auto;
                    align-items: flex-start;

                    .active-left {
                        display: flex;
                        justify-content: center;
                        flex-direction: column;

                        h1 {
                            font-size: 50px;
                            text-align: center;
                            cursor: pointer;
                        }

                        a {
                            padding: 15px;
                            font-size: 16px;
                            text-transform: uppercase;
                            text-align: center;
                            font-weight: 900;
                        }

                        a:active {
                            background: #ffcc0d;
                        }

                        .active {
                            background: #ffcc0d;
                        }
                    }

                    .line {
                        border-right: 5px solid #000;
                        margin-left: 35px;
                        height: 90vh;
                        border-radius: 10px;
                    }
                }
            }





            // trang page web
            .page {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                height: max-content;

                // trang home
                .element {
                    // width: 90%;
                    margin-top: 4%;
                    border-bottom: #000 5px solid;
                    opacity: 0;
                    transition: 3s all ease;
                    padding: 10px;
                }

                .element.active {
                    transform: translateY(0);
                    opacity: 1;
                }

                // trang cá nhân
                .myinfo {
                    width: 100%;
                    height: 100vh;
                    display: flex;

                    .content {
                        display: flex;
                        flex-direction: column;
                        width: 100%;
                        align-items: center;

                        h2 {
                            margin-top: 10%;
                            padding: 19px;
                            font-weight: 900;
                            font-size: 30px;
                            border-bottom: #000 10px solid;
                        }

                        p {
                            font-size: 25px;
                        }

                        .social {
                            width: 100%;
                            height: 100px;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            a {
                                margin-left: 3%;

                                svg {
                                    padding: 10px;
                                    width: 25px;
                                    height: 25px;
                                }
                            }
                        }
                    }
                }
                //trang skill 

            }
        }
    }

    @media only screen and (max-width: 880px) {

        // background-color: wheat;
        font-size: 12px;
        body {
            .main {
                .left-bar {
                    display: none;
                }
            .element{
            .myinfo{
                width: 100%;
            }  
                #mycontact{
                    width: 100%;
                    .card_contact{
                        flex-direction: column;
                        .card{
                            width: 100%;
                        }
                    }
                }
            }
            .myskill{
                width: 100%;
                .box{
                    width: 100%;
                    flex-direction: column;
                    .app, .web{
                        width: 100%;
                        margin-bottom:2%;
                    }
                }
            }
            }
        }

    }
}

html:focus-within {
    scroll-behavior: smooth;
}